import { useState, useEffect } from 'react';
import axios, { AxiosError, isCancel } from 'axios';
import Character from './Character'
import { useNavigate } from 'react-router-dom';

function Catalog() {
  const navigate = useNavigate();
  const [characters, setCharacters] = useState([]);

  const pages = [1, 2, 3];

  useEffect(() => {
    const getData = async () => {
      try {
        const request = await axios.get(`https://valorant-api.com/v1/agents`);       
        
        const agentsData = await request.data.data;

        setCharacters(agentsData)
      } catch (error) {
        console.log(error)
      }
    
    }

    getData();
  }, [])

  const compareName = (a, b) => {

    const name1 = a.displayName.toUpperCase();
    const name2 = b.displayName.toUpperCase();

    let comparison = 0;

    if (name1 > name2) {
        comparison = 1;
    } else if (name1 < name2) {
        comparison = -1;
    }
    return comparison;
  }

  const handleSort = () => {

    let charactersPrevious = characters;
    let charactersSorted = charactersPrevious.sort(compareName)
    
    setCharacters([]);
    setCharacters([...charactersSorted])

  }

  const handleSubmit = () => {
    navigate("/valorant-facts/new");
  }

  useEffect(() => {
    console.log(characters)
  })




  return (
    <section className="catalog">
      <div className="container">
        <div className="content-options">
          <input
            type="button"
            value="Sort from Name (A-Z)"
            id="margin-offset-left"
            onClick={handleSort}
          />
          <input
            type="button"
            value="Add Character"
            id="margin-offset-left"
            onClick={handleSubmit}
          />
        </div>

        <div className="characters-grid">
          {characters.map(character => (
            <Character 
              name={character.displayName}
              desc={character.description}
              image={{fullPortrait : character.fullPortrait, killfeed: character.killfeedPortrait, icon: character.displayIcon}}
              key={character.uuid}
              id={character.uuid}
            />
          ))}
        </div>
      </div>
    </section>
  )
}

export default Catalog