import { useParams } from 'react-router-dom';
import { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import { Helmet } from 'react-helmet';

function Detail() {
  const { agentid } = useParams();

  console.log(agentid);
  
  const [details, setDetails] = useState(false);
  
  const getDetails = useCallback(async () => {

    const agentData = await axios.get(`https://valorant-api.com/v1/agents/${agentid}`)

    console.log(agentData.data.data)
    setDetails(agentData.data.data);
    
  }, [agentid])

  useEffect(() => {
    getDetails();
  }, [getDetails])

  return (
    <section>
      { details && <>
      <Helmet>
        <title>{`Valorant Agents: ${details.displayName}`}</title>
      </Helmet>
        <div className="container">
          <div className="detail-heading">
            <div className="name"> {details.displayName} </div> 
            <div className="role"><div id="bottomaligned">{details.characterTags ? details.characterTags : ""}</div></div>
          </div>
        </div>
        <div className="container">
          <div className="details">
            <div className="details-left">
              <div className="description">
                <p>
                  {details.description}
                </p>
              </div>
              <div className="other-details">
                <div>
                  <span className="psuedo-header">Developed as:</span>
                  <span>{details.developerName}</span>
                </div>
              </div>
              <div className="role">
                <p>
                  This agent's role is <span 
                    className="psuedo-header">{details.role.displayName}
                  </span>
                  
                </p>
              </div>
              <div className="role-description">
                <p>
                  {details.role.description}
                </p>
              </div>
              
            </div>
            <div className="details-right">
            <h4>Full Portrait:</h4>
              <div className="portrait">
                <img 
                  src={details.fullPortrait}
                  alt="Full Portrait of Character"
                />
              </div>
              <h4>Killfeed Portrait:</h4>
              <div className="otherphoto">
                <img 
                  src={details.killfeedPortrait}
                  alt="Killfeed Portrait"
                />
              </div>
              
            </div>
          </div>
        </div>
      </>}
    </section>
  )
}

export default Detail