import { useNavigate } from "react-router-dom"
import { useForm } from 'react-hook-form';
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";

const schema = yup.object({
  
  name: yup.string().required(),
  agentid: yup
    .string()
    .required("Agent's unique ID is required!")
    .matches(
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,8}$/, 
      "ID must be 8 characters long, with at least one uppercase letter, a lowercase letter, a number and a special character"
    ),
})

function New() {

  const navigate = useNavigate();

  const { 
    register, 
    formState: {errors, isValid}, 
    handleSubmit 
  } = useForm({ resolver: yupResolver(schema), mode: 'onChange'});

  const formSubmit = (data) => {
    console.log(data);
  }

  return (
      <div className="container">
        <h2>Add a New Character</h2>
        <div className="row">
          <div className="form-section">
            <form onSubmit={handleSubmit(formSubmit)}>
              <div className="form-line">
                <label htmlFor="name">Agent Name:</label>
                <input
                  type="text"
                  id="name"
                  name="name"
                  placeholder="Enter a name:"
                  autoComplete="off"
                  onChange={console.log(isValid)}
                  {...register("name")}
                />
                <p id="form-error">
                  {errors.name?.message}
                </p>
              </div>
              <div className="form-line">
                <label htmlFor="name">Agent Role:</label>
                <select name="Role" id="role" placeholder="Choose a role:">
                  <option value="Initiator">Initiator</option>
                  <option value="Duelist">Duelist</option>
                  <option value="Sentinel">Sentinel</option>
                  <option value="Controller">Controller</option>
                </select>
              </div>
              <div className="form-line">
                <label htmlFor="agentid">ID:</label>
                <input
                  type="text"
                  id="agentid"
                  name="agentid"
                  placeholder="Enter unique ID:"
                  autoComplete="off"
                  {...register("agentid")}
                />
                <p id="form-error">
                  {errors.agentid?.message}
                </p>
              </div>
              <label htmlFor="name">Description:</label>
              <textarea
                name="enter-desc"
                rows="10"
                cols="30"
                placeholder="Enter your agents's description (optional)"
              />
              <div className="form-line">
                <input type="submit" value="Submit" disabled={!isValid} className="formsubmit" id="margin-offset-right"/>
                <input type="submit" value="Back To Home" id="margin-offset-right" onClick={navigate("/valorant-facts")}/>
              </div>
      
            </form>
          </div>
          <div className="valorant-background-border">
            <img className="fill" src="https://wallpaperaccess.com/full/3037905.jpg" alt=""/>
          </div>
        </div>
      </div>
  )
}

export default New