import React from 'react'
import { useNavigate } from "react-router-dom"

function Character(props) {

  let render = true;
  const navigate = useNavigate();

  const directTo = (id) => {
    navigate(`/valorant-facts/agents/${id}`);
  }

  if(props.image.fullPortrait === null){
    render = false;
  }

  console.log(props.image.fullPortrait);

  const handleClick = () => {
    console.log("i jus got clicked");
    console.log(props)
    directTo(props.id);
  }

  return ( 
    <>
    { render && <div className="character">
      <div className="cover" onClick={handleClick}>
        <img 
          className="character_img"
          src={props.image.fullPortrait} 
          alt=""
        />
      </div>
      <p>{props.name}</p>
    </div>}
      
    
    </>
    
  )
}

export default Character